import React, { useState, useEffect, useCallback } from 'react';
import {
  NavBar,
  Image,
  Button,
  Tag,
  Cell,
  Dialog,
  Field,
  Toast,
  ActionSheet
} from 'react-vant';
import { useNavigate, useParams } from 'react-router-dom';
import { productAPI, exchangeAPI, utils } from '../services/api';
import Icon from '../components/Icon';
import { useAuth } from '../context/AuthContext';

const ProductDetail = () => {
  const navigate = useNavigate();
  const { id } = useParams();
  const { user } = useAuth();
  const [product, setProduct] = useState(null);
  const [loading, setLoading] = useState(true);
  const [exchangeVisible, setExchangeVisible] = useState(false);
  const [exchangeForm, setExchangeForm] = useState({
    quantity: 1,
    contactName: '',
    contactPhone: '',
    shippingAddress: ''
  });

  const loadProduct = useCallback(async () => {
    try {
      setLoading(true);
      const response = await productAPI.getDetail(id);
      setProduct(response.data);
    } catch (error) {
      console.error(':', error);
      Toast.fail('');
      navigate(-1);
    } finally {
      setLoading(false);
    }
  }, [id, navigate]);

  useEffect(() => {
    loadProduct();
  }, [loadProduct]);

  const handleExchange = () => {
    if (!user?.user_id) {
      Toast.fail('');
      navigate('/login');
      return;
    }
    if (!product || product.stock === 0) {
      Toast.fail('');
      return;
    }
    setExchangeVisible(true);
  };

  const handleConfirmExchange = async () => {
    if (!user?.user_id) {
      Toast.fail('');
      setExchangeVisible(false);
      navigate('/login');
      return;
    }
    if (!exchangeForm.contactName.trim()) {
      Toast.fail('');
      return;
    }
    if (!exchangeForm.contactPhone.trim()) {
      Toast.fail('');
      return;
    }
    if (!exchangeForm.shippingAddress.trim()) {
      Toast.fail('');
      return;
    }

    try {
      await exchangeAPI.create({
        product_id: product.id,
        quantity: exchangeForm.quantity,
        contact_name: exchangeForm.contactName.trim(),
        contact_phone: exchangeForm.contactPhone.trim(),
        shipping_address: exchangeForm.shippingAddress.trim(),
      });

      Toast.success('');
      setExchangeVisible(false);

      Dialog.confirm({
        title: '',
        message: '',
      }).then(() => {
        navigate('/exchange-record');
      }).catch(() => {
        navigate(-1);
      });
    } catch (error) {
      console.error(':', error);
      Toast.fail(error?.response?.data?.message || '');
    }
  };

  if (loading) {
    return (
      <div className="page-container">
        <NavBar 
          title="" 
          leftArrow={<Icon name="arrow-left" />}
          onClickLeft={() => navigate(-1)}
          fixed 
          placeholder
        />
        <div className="loading-container">
          <div>...</div>
        </div>
      </div>
    );
  }

  if (!product) {
    return (
      <div className="page-container">
        <NavBar 
          title="" 
          leftArrow={<Icon name="arrow-left" />}
          onClickLeft={() => navigate(-1)}
          fixed 
          placeholder
        />
        <div className="page-content">
          <div style={{ padding: '40px 16px', textAlign: 'center', color: '#969799' }}>
            
          </div>
        </div>
      </div>
    );
  }

  return (
    <div className="page-container">
      <NavBar 
        title="" 
        leftArrow={<Icon name="arrow-left" />}
        onClickLeft={() => navigate(-1)}
        fixed 
        placeholder
      />
      
      <div className="page-content">
        {/*  */}
        <div style={{ background: '#fff', borderRadius: '12px', overflow: 'hidden' }}>
          <Image
            src={utils.buildImageUrl(product.image_url)}
            alt={product.name}
            width="100%"
            height="220px"
            fit="cover"
          />

          <div style={{ padding: '16px' }}>
            <div style={{ fontSize: 18, fontWeight: 600, marginBottom: 8 }}>
              {product.name}
            </div>

            <div style={{ marginBottom: 8 }}>
              {product.is_hot && (
                <Tag color="#FF3B30" style={{ marginRight: 8 }}></Tag>
              )}
              {product.is_new && (
                <Tag color="#FF9500"></Tag>
              )}
            </div>

            <div style={{ marginBottom: 8 }}>
              <span style={{ 
                fontSize: 20, 
                fontWeight: 600, 
                color: '#FF3B30'
              }}>
                {utils.formatNumber(product.points_required)} 
              </span>
              {product.original_price && (
                <span style={{ 
                  fontSize: 14, 
                  color: '#969799',
                  textDecoration: 'line-through',
                  marginLeft: 8
                }}>
                  {product.original_price}
                </span>
              )}
            </div>
            
            <div style={{ fontSize: 14, color: '#969799' }}>
              {product.stock === -1 ? '' : ` ${product.stock}`}
            </div>
          </div>

          {product.description && (
            <div style={{ 
              fontSize: 14, 
              color: '#646566', 
              lineHeight: '1.6',
              padding: '0 16px 16px'
            }}>
              {product.description}
            </div>
          )}
        </div>

        {/*  */}
        <div style={{ marginTop: 12, background: '#fff', borderRadius: '12px', overflow: 'hidden' }}>
          <Cell.Group>
            <Cell title="" value={product.category} />
            <Cell title="" value={`${product.sold_count} `} />
            {product.start_time && product.end_time && (
              <Cell 
                title="" 
                value={`${utils.formatDate(product.start_time)} - ${utils.formatDate(product.end_time)}`}
                label=""
              />
            )}
          </Cell.Group>
        </div>
      </div>

      {/*  */}
      <div className="fixed-bottom-button safe-area">
        <Button 
          type="primary" 
          size="large" 
          block
          disabled={product.stock === 0}
          onClick={handleExchange}
        >
          {product.stock === 0 ? '' : ''}
        </Button>
      </div>

      {/*  */}
      <ActionSheet
        visible={exchangeVisible}
        onClose={() => setExchangeVisible(false)}
        onCancel={() => setExchangeVisible(false)}
        title=""
        closeable
      >
        <div style={{ padding: 16 }}>
          <Field
            value={exchangeForm.contactName}
            onChange={(value) => setExchangeForm(prev => ({ ...prev, contactName: value }))}
            label=""
            placeholder=""
            required
            clearable
          />
          <Field
            value={exchangeForm.contactPhone}
            onChange={(value) => setExchangeForm(prev => ({ ...prev, contactPhone: value }))}
            label=""
            placeholder=""
            required
            clearable
          />
          <Field
            value={exchangeForm.shippingAddress}
            onChange={(value) => setExchangeForm(prev => ({ ...prev, shippingAddress: value }))}
            label=""
            placeholder=""
            type="textarea"
            rows={3}
            required
            clearable
          />
          
          <div style={{ marginTop: 24, display: 'flex', gap: 12 }}>
            <Button 
              block 
              onClick={() => setExchangeVisible(false)}
            >
              
            </Button>
            <Button 
              type="primary" 
              block
              onClick={handleConfirmExchange}
            >
              
            </Button>
          </div>
        </div>
      </ActionSheet>
    </div>
  );
};

export default ProductDetail;
