import React from 'react';
import { BrowserRouter as Router, Routes, Route, Navigate, useLocation, useNavigate } from 'react-router-dom';
import { Tabbar } from 'react-vant';
import Icon from './components/Icon';
import './styles/theme.css';

import { AuthProvider, useAuth } from './context/AuthContext';

// 
import Home from './pages/Home';
import Shop from './pages/Shop';
import ProductDetail from './pages/ProductDetail';
import Profile from './pages/Profile';
import PointsDetails from './pages/PointsDetails';
import ExchangeRecord from './pages/ExchangeRecord';
import ExchangeDetail from './pages/ExchangeDetail';
import ChangePassword from './pages/ChangePassword';
import Login from './pages/Login';

function AppContent() {
  const [activeTab, setActiveTab] = React.useState('home');
  const location = useLocation();
  const navigate = useNavigate();
  const { isAuthenticated } = useAuth();

  // 
  const hideTabbarPages = ['/product/', '/exchange/', '/points-details', '/change-password'];
  const shouldHideTabbar = hideTabbarPages.some(page => location.pathname.includes(page));

  React.useEffect(() => {
    // tab
    if (location.pathname === '/' || location.pathname === '/home') {
      setActiveTab('home');
    } else if (location.pathname === '/shop') {
      setActiveTab('shop');
    } else if (location.pathname === '/profile') {
      setActiveTab('profile');
    }
  }, [location.pathname]);

  const handleTabChange = (name) => {
    setActiveTab(name);
    navigate(`/${name}`);
  };

  if (!isAuthenticated) {
    return (
      <Routes>
        <Route path="/login" element={<Login />} />
        <Route path="*" element={<Navigate to="/login" replace />} />
      </Routes>
    );
  }

  return (
    <div className="page-container">
      <Routes>
        <Route path="/login" element={<Navigate to="/home" replace />} />
        <Route path="/" element={<Navigate to="/home" replace />} />
        <Route path="/home" element={<Home />} />
        <Route path="/shop" element={<Shop />} />
        <Route path="/product/:id" element={<ProductDetail />} />
        <Route path="/profile" element={<Profile />} />
        <Route path="/points-details" element={<PointsDetails />} />
        <Route path="/exchange-record" element={<ExchangeRecord />} />
        <Route path="/exchange/:id" element={<ExchangeDetail />} />
        <Route path="/change-password" element={<ChangePassword />} />
        <Route path="*" element={<Navigate to="/home" replace />} />
      </Routes>

      {/*  */}
      {!shouldHideTabbar && (
        <Tabbar
          value={activeTab}
          onChange={handleTabChange}
          fixed
          placeholder
          safeAreaInsetBottom
        >
          <Tabbar.Item
            name="home"
            icon={<Icon name="wap-home-o" size={24} />}
          >
            
          </Tabbar.Item>
          <Tabbar.Item
            name="shop"
            icon={<Icon name="shop-o" size={24} />}
          >
            
          </Tabbar.Item>
          <Tabbar.Item
            name="profile"
            icon={<Icon name="contact" size={24} />}
          >
            
          </Tabbar.Item>
        </Tabbar>
      )}
    </div>
  );
}

export default function App() {
  return (
    <AuthProvider>
      <Router>
        <AppContent />
      </Router>
    </AuthProvider>
  );
}
